@import 'const';

.registration {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  &__content {
    width: 340px;
    background: $yellowColor;
    box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.14);
    border-radius: 12px;
    padding: 50px 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  &__title {
    font-family: 'Inter';
    font-weight: 500;
    font-size: 20px;
    line-height: 20px;

    text-align: center;

    color: $pinkColor;
    margin-bottom: 41px;
  }
  &__buttons {
    margin-top: 47px;
    display: flex;
    flex-direction: column;
    gap: 14px;
    align-items: center;
  }
  &__enter {
    font-family: 'Inter';
    font-weight: 500;
    font-size: 11px;
    line-height: 11px;

    color: #F8BDFC;
  }
  &__fields {
    display: flex;
    flex-direction: column;
    gap: 32px;
  }
}