@import 'const';

.changePassword {
  background: $yellowColor;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  &__fields {
    display: flex;
    flex-direction: column;
    gap: 12px;
    margin-bottom: 48px;
    margin-top: 48px;
    width: 100%;
  }
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 510px;
  }
  &__img {
    width: 130px;
    height: 130px;
    border-radius: 50%;
    background: #EFEFEF;
    margin-bottom: 21px;
    cursor: pointer;
  }
}